					<div class="guessing-order-item" v-for="(item,index) in guessingOrderList" :key="index">
						<div class="guessing-order-item-title">
							<div class="guessing-order-item-title-type" :class="{'guessing-order-item-title-type-down':item.type == 2}">
								<span v-if="item.type == 1"></span>
								<span v-if="item.type == 2"></span>
