<script>

import echartsDemo from '../components/EchartsDemo.vue'

import { mapState } from 'vuex'
import { setTimeout } from 'timers';

export default {
	name: "home",
	components:{
		echartsDemo	
	},
	data(){
		return{
			isLogin:false,
		
			btn_price_data_before:'5012.21',//btn
			guess_money_index:0,
			guess_count_index:0,
			guess_stop_index:0,
			guess_data:{
				point:[],///
				quiz:[],//
				usdt:[],//
				recharge:0,//
				btc_price:'',//
				is_open:0,
				opentime:{
					start_time:'',
					end_time:''
				}
			},
			//
			order_data:{
				point:'',
				quiz:'',
				usdt:'',
				order_money:''
			},
			showGuessModal:false,//
			guess_type:1,//1 2 
			showGuessingOrderModal:false,//
			guessingOrderList:[
				// {type:1,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
				// {type:2,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
				// {type:1,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
				// {type:1,name:'BTC',money:'200',count:3,up_money:'4396',down_money:'4436',price:'5321'},
			],
			//
			btn_price_data:{
				vol:'',//
				high:'',//
				low:'',//
				sell:'',//
				last:'',//
				buy:''//
			},

			//
			guessingInterval:null
		}
	},
	filters:{
		//
		toFixedNum(val,num){
			if(val){
				return parseFloat(val).toFixed(num);
			}else{
				return ''
			}
			return val
		}
	},
	computed:{
		...mapState(['user_info']),//
	},
	created(){
		
        if(this.$route.params.id){
			this.isLogin = true;
			this.utils.info('...');
			this.user_info.user_id = this.$route.params.id;

			//
			this.getUserInfo();
		}else{
			this.utils.warn('...');
		}
		
		
	},
	mounted(){

	},
	methods:{
		//app
		goBack(){
			this.utils.info('app...');
			this.$bridge.callhandler('goBack', {tag:"app"}, () => {})
		},
		//
		goRecharge(){
			this.utils.info('...');
			this.$bridge.callhandler('goRecharge', {tag:""}, () => {})	
		},
		//
		goPay(){
			this.utils.info('...');
			this.$bridge.callhandler('goPay', {tag:""}, (data) => {})
		},
		//
		goLogin(){
			this.utils.info('...');
			this.$bridge.callhandler('goLogin', {tag:""}, (data) => {})
		},
		//
		getUserInfo(){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_usdt;
                    this.user_info.pay_password = data.pay_password;
					this.user_info.token = data.token;

					//
					this.getPageData();

					//
					this.getGuessingList();
					
                }
			})
		},
		//
		getDataFromChild(data){
			//this.utils.info('...',data);

			this.btn_price_data = data;
		},
		//
		showGuessModalFun(type){
			this.guess_type = type;
			this.showGuessModal = true;
		},
		//
		goHomeList(){
			if(!this.isLogin){
				this.utils.showToast('...');
				this.goLogin();
				return false;
			}
			this.$router.push({
				name:'homeList'
			})	
		},
		//
		goHomeRule(){
			this.$router.push({
				name:'homeRule'
			})
		},
		//
		closeGuessModalFun(){
			this.showGuessModal = false;

			//
			this.initOrderData();
		},
		//
		changeGuessMoney(item,index){
			
			if(this.guess_money_index == index){
				return false;
			}
			this.guess_money_index = index;
			this.order_data.usdt = item.usdt_num;
			this.order_data.order_money = (parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) + ((parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) * parseFloat(this.guess_data.recharge))/100
		},
		//
		changeGuessCount(item,index){
			
			if(this.guess_count_index == index){
				return false;
			}
			this.guess_count_index = index;
			this.order_data.quiz = item.quiz_num;
			this.order_data.order_money = (parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) + ((parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) * parseFloat(this.guess_data.recharge))/100
		},
		///
		changeGuessStop(item,index){
			
			if(this.guess_stop_index == index){
				return false;
			}
			this.guess_stop_index = index;
			this.order_data.point = item.point_num;
		},
		//
		confirm(){
			this.utils.info('...');
			if(!this.isLogin){
				this.utils.showToast('...');
				this.goLogin();
				return false;
			}
			this.utils.info('...',this.order_data);

			if(parseFloat(this.order_data.order_money) > parseFloat(this.user_info.dw_money)){
				this.utils.showToast('USDT');
				return false;
			}

			if(this.btn_price_data.sell == ''){
				this.utils.showToast('');
				return false;
			}
			
			let option = {
				style:this.guess_type,
				buy_price:this.btn_price_data.sell,
				buy_quiz:this.order_data.quiz,
				usdt_fee:this.order_data.usdt,
				btc_point:this.order_data.point,
				token:this.user_info.token
			}
			this.utils.info('...',option);
			this.http.post(this.http._HOST + 'wave/buy_point',option).then(result =>{
				this.utils.info('...',result);
				if(result.success){
					this.utils.showToast('');
					
					//
					this.closeGuessModalFun();

					//
					this.getGuessingList();
				}else{
					this.utils.showAlert(result.message);
				}
			})
		},
		//
		showGuessingOrderModalFun(){
			if(!this.isLogin){
				this.utils.showToast('...');
				this.goLogin();
				return false;
			}

			this.showGuessingOrderModal = true;
		},
		//
		closeGuessingOrderModalFun(){
			this.showGuessingOrderModal = false;
		},
		//
		getPageData(){
			let option = {
				type:'2',
				token:this.user_info.token
			}
			this.http.post(this.http._HOST + 'wave/basic_usdt',option).then(result =>{
				this.utils.info('',result);
				if(result.success){
					this.guess_data = result.data;

					//
					this.initOrderData();

				}else{
					this.utils.err('...3s');
					let getPageDataTimeout = setTimeout(() =>{
						this.getPageData();
						clearTimeout(getPageDataTimeout);
					},3000)
				}
			})
		},
		//
		initOrderData(){
			this.guess_money_index = 0;
			this.guess_count_index = 0;
			this.guess_stop_index = 0;
			if(this.guess_data.usdt.length > 0 && this.guess_data.quiz.length > 0 && this.guess_data.point.length > 0){
				this.order_data.usdt = this.guess_data.usdt[0].usdt_num;
				this.order_data.quiz = this.guess_data.quiz[0].quiz_num;
				this.order_data.point = this.guess_data.point[0].point_num;

				this.order_data.order_money = (parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) + ((parseInt(this.order_data.usdt) * parseInt(this.order_data.quiz)) * parseFloat(this.guess_data.recharge))/100
			}else{
				this.utils.err('...');
			}
		},
		//
		getGuessingList(){
			let option = {
				token:this.user_info.token,
				type:'2'
			}
			this.http.post(this.http._HOST + 'wave/order_list',option).then(result =>{
				this.utils.info('',result);
				if(result.success){
					this.guessingOrderList = result.data;

					// if(this.guessingOrderList.length > 0){
					// 	this.guessingInterval = setInterval(() =>{

					// 	},5000)	
					// }

					if(this.guessingOrderList.length > 0){
						let guessingListTimeOut = setTimeout(() =>{
							console.log('...');
							this.getGuessingList();
							clearTimeout(guessingListTimeOut);	
						},10000)
					}

				}else{
					this.utils.err('...3s');
					let getGuessingListTimeout = setTimeout(() =>{
						this.getGuessingList();
						clearTimeout(getGuessingListTimeout);
					},3000)	
				}
			})
		}
	}
};
</script>


